<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import Counter from '@/components/Counter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import PreloaderDotsWave from '@/components/PreloaderDotsWave.vue';
import { mapActions } from 'vuex';
import ColorOptions from '@/components/ColorOptions.vue';

export default {
  components: { ColorOptions, PreloaderDotsWave, Counter },
  data() {
    return {
      productAmount: 1,
      currentColorId: null,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category.title;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        // eslint-disable-next-line no-return-assign
        .then((response) => this.productData = response.data)
        // .then(() => this.currentColorId = this.productData.colors[0].id)
        // eslint-disable-next-line no-return-assign
        .catch(() => this.productLoadingFailed = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.productLoading = false);
    },
  },
  created() {
    this.loadProduct();
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
